<template>
  <div class="personal">
    <PersonalHeader></PersonalHeader>
    <div class="wrapper clearfix content">
      <PersonalMenu class="fl" currentMenu=""></PersonalMenu>
      <div class="fr right">
        <div class="box">
          <div class="title">个人中心</div>
          <div class="per">
            <router-link class="item "  to="/message">
              <img  class="" src="../../../static/image/personal_icon_01.png" alt="">
              <div  class="right">
                  <div>我的消息</div>
                  <div class="count">{{data.no}}</div>
              </div>
            </router-link>
            <router-link class="item " to="/focus" >
              <img  class="" src="../../../static/image/personal_icon_02.png" alt="">
              <div class="right">
                <div>我的关注</div>
                <div class="count">{{data.fo}}</div>
              </div>
            </router-link>
            <router-link class="item " to="/collect" >
              <img  class="" src="../../../static/image/personal_icon_03.png" alt="">
              <div class="right">
                <div>我的收藏</div>
                <div class="count">{{data.co}}</div>
              </div>
            </router-link>
            <router-link class="item " to="/leaveMessage">
              <img  class="" src="../../../static/image/personal_icon_04.png" alt="">
              <div class="right">
                <div>我的留言</div>
                <div class="count">{{data.lm}}</div>
              </div>
            </router-link>
          </div>

        </div>
        <div class="box">
          <div class="title">我的足迹</div>
          <div class="footprint">
            <router-link class="item" :to="'/detail?id='+item.id" v-for="item in data.track" :key="'pro'+item.id">
              <img :src="item.img" alt="">
              <div class="right">
                <div class="name clearfix">
                  <span class="fl">{{item.item_name}}</span>
                  <span class="money fr">￥{{item.money}}万</span>
                </div>
                <div class="detail">
                  <div class="info">
                    所属行业：<span>{{item.cate_name}}</span>
                  </div>
                  <div class="info">
                    直营店：<span>{{item.shop_count}}家</span>
                  </div>
                  <div class="info">
                    申请人数：<span>{{item.apply}}</span>
                  </div>

                  <div class="info">
                    加盟店：<span>{{item.fran_store_num}}家</span>
                  </div>
                </div>
              </div>
            </router-link>
          </div>
        </div>

        <div class="box">
          <div class="title">我的评论</div>
          <div class="comment">
            <div>
              <dl class="listDL clearfix" v-for="item in data.comment" :key="'comment'+ item.id">
                <dt>
                  <img :src="item.article.img">
                </dt>
                <dd>
                  <h3>{{item.article.title}}</h3>
                  <p class="desc">{{item.article.sign}}</p>
                  <p class="icon">
                    <span><i class="iconfont icon-data"></i>{{item.addtime}}</span>
                    <span><i class="iconfont icon-look"></i>{{item.read_num}}</span>
                  </p>
                </dd>
                <div class="reply fl">{{item.content}}</div>
              </dl>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import PersonalHeader from '@/components/PersonalHeader'
  import PersonalMenu from '@/components/PersonalMenu'
  import api from '@/api/personal-api'

  export default {
    name: "personal",
    data(){
      return {
        data:{co:'',fo:'',lm:'',no:'',track:[]},

      }
    },
    created() {
        this.init();
        this.getData();
    },
    methods:{
      init: function(){

      },
      getData(){
          api.getData().then(data => {
              this.data = data;
          });
        }
    },
    components: {
      PersonalHeader,
      PersonalMenu
    }
  }
</script>

<style scoped type="text/less" lang="less">
  .personal {
    background-color: #F4F5FA;
    padding-bottom: 120px;

    .content {
      padding-top: 20px;

      .right {
        width: 950px;

        .box {
          background-color: #fff;
          margin-bottom: 20px;
          .title {
            height: 60px;
            line-height: 60px;
            font-weight: bold;
            font-size: 16px;
            color: #333;
            padding: 0 32px;
            border-bottom: 1px solid #E3E4E5;
          }

          .per {
            height: 139px;
            padding: 0 30px;
            display: flex;
            align-items: center;
            .item{
              width: 230px;
              display: flex;
              align-items: center;
              color: #333;
              font-size: 16px;
              font-weight: bold;
              img{
                width: 60px;
                height: 60px;
                border-radius: 50%;
                vertical-align: middle;
                margin-right: 18px;
              }
              .count{
                color: #999;
                font-size: 14px;
                font-weight: normal;
              }
            }

          }

          .footprint {
            padding: 0 30px;
            .item{
              display: flex;
              font-size: 14px;
              color: #999;
              padding: 20px 0;
              border-bottom: 1px solid #E3E4E5;
              .right{
                width: 700px;
                margin-left: 22px;
                .name{
                  font-size: 18px;
                  color: #333;
                  font-weight: bold;
                  margin-top: 24px;
                  .money{
                    color: #FF7E00;
                    font-size: 16px;

                  }
                }
              }
              img{
                width: 140px;
                height: 140px;
              }
              .detail{
                width: 320px;
                margin-top: 15px;

                .info{
                  display: inline-block;
                  margin-bottom: 10px;
                  span{
                    display: inline-block;
                    width: 80px;
                  }
                }
              }
            }
            .item:last-child{
              border: 0;
            }
          }

          .comment{
            padding: 0 30px;
            .listDL{
              padding: 20px 0;
              border-bottom: 1px solid #E3E4E5;
              dt{
                float: left;
                width: 198px;
                height: 140px;
                img{
                  display: block;
                  width: 198px;
                  height: 140px;
                }
              }
              dd{
                float: left;
                width: 580px;
                margin-left: 25px;
                h3{
                  font-size: 18px;
                  color: #000;
                  margin-top: 12px;
                }
                .desc{
                  line-height: 26px;
                  overflow: hidden;
                  text-overflow: ellipsis;
                  display: -webkit-box;
                  -webkit-line-clamp: 2;
                  -webkit-box-orient: vertical;
                  margin-top: 13px;
                }
                .icon{
                  color: #999;
                  line-height: 20px;
                  margin-top: 12px;
                  span{
                    margin-right: 35px;
                    i{
                      color: #ccc;
                      margin-right: 10px;
                    }
                  }

                }
              }

              .reply{
                width: 850px;
                font-size: 14px;
                color: #808080;
                padding: 10px 20px;
                background-color: #F4F5FA;
                margin-top: 20px;
              }
            }
            .listDL:last-child{
              border: 0;
            }
          }
        }



      }
    }
  }


</style>



